
/*------------------------------------*\
    #ARTICLE
\*------------------------------------*/

.art-img-horz {
  background-size: auto 238px;
}

.art-img-vert {
  background-size: 247px auto;
}

.art-img {
  max-width: 247px;
  height: 238px;
  position: relative;
  background-image: url("../images/img_article/cassie.jpg");
  background-position: center;
  margin-bottom: 2rem;
  margin-left: 1.25rem;
  margin-right: 1.25rem;
}

.art-img .art-title {
  position: absolute;
  bottom: 10px;
  left: 10px;
  color: $color-white;
  font-size: 17pt;
  font-style: $font;
  font-weight: $font-semibold;
  text-shadow: 2px 2px 2px $color-neutral-grey3;
  transition: bottom 0.2s ease;
}

.art-focus .art-title {
  position: absolute;
  bottom: 10px;
  left: 10px;
  color: $color-white;
  font-size: 17pt;
  font-style: $font;
  font-weight: $font-semibold;
  text-shadow: 2px 2px 2px $color-neutral-grey3;
  transition: bottom 0.2s ease;
}

.art-filter:hover ~ .art-title {
  bottom: 35px;
}

.art-subtitle {
  position: absolute;
  opacity: 0;
  bottom : 10px;
  left: 10px;
  color: $color-brand-yellow;
  font-size: 13pt;
  font-weight: $font-semibold;
  transition: opacity 0.2s ease;
}

.art-filter:hover ~ .art-subtitle {
  opacity: 1;
}

.art-filter {
  width: 247px;
  height: 238px;
  background-color: $color-neutral-grey2;
  -webkit-filter: sepia(80%) opacity(40%);
  transition: -webkit-filter 0.4s ease;

  &:hover, &:focus {
  -webkit-filter: sepia(20%) opacity(75%);
  }
}

.art-img .c-atom {
  opacity: 0;
  position: absolute;
  left: 50%;
  transform: translate(-50%,0%);
  top: 20px;
  width: 80px;
  height: auto;
  color: $color-white;
  transition: opacity 0.3s ease;
}

.art-focus .c-atom {
  opacity: 0;
  position: absolute;
  right: 20px;
  bottom: 10px;
  width: 80px;
  height: auto;
  color: $color-white;
  transition: opacity 0.3s ease;
}


.art-filter:hover ~ .c-atom {
  opacity: 1;
}

.block-art {
  display : flex;
  max-width: 100%;
  flex-wrap: wrap;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
}

.art-focus {
  max-width: 694px;
  height: 238px;
  position: relative;
  background-image: url("../../images/img_article/googlex.jpg");
  background-position: center;
  margin-bottom: 2rem;
  margin-left: 1.25rem;
  margin-right: 1.25rem;
}

.art-focus .art-filter {
  width: 600px;
  height: 238px;
}

.focus {
  display: flex;
  max-width: 810px;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}

.p-focus {
  font-size: 12pt;
  font-weight: $font-regular;
  line-height: 24pt;
}
