/*------------------------------------*\
    #MENU
\*------------------------------------*/

.menu {
	max-width: 810px;
	display: flex;
	flex-flow: row wrap;
}

.search {
	max-width: 100%;
	display: flex;
}

.c-vertical-menu .search {
	max-width: 810px;
}

.c-icon-menu {
	display: flex;
	flex-direction: column;
	text-align: center;
	font-size: 10px;
	color: $color-neutral-grey2;
	margin-top: 12px;
	margin-bottom: 12px;
	cursor: pointer;
}

.c-icon-menu .c-atom {
	margin-left: auto;
	margin-right: auto;
	width: 27px;
	padding-right: 2px;
}

.c-icon-menu .c-atom-section {
	margin-left: auto;
	margin-right: auto;
	width: 75%;
	height: 75%;
}

.c-vertical-menu {
	max-width: 60px;
}

.verticalmenu-section {
	margin-top: 15px;
	margin-left: auto;
	margin-right: auto;
}

.verticalmenu-section .c-atom-section {
	height: 40px;
	width: 40px;
}

.background-section {
	height: 40px;
	width: 40px;
	border-radius: 40px;
	background: $color-brand-beige;
}

.c-compo-menu {
	display: flex;
}

.c-compo-searchblock {
	max-width: 830px;
}

.itemmenu {
	margin: 10px;
}
